'use client';

import { useUser } from '@/context/UserContext';
import { usePathname, useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';

export const Header = () => {
  const { user, isAuthenticated, logout } = useUser();
  const pathname = usePathname();
  const router = useRouter();

  // Don't show header on login pages
  if (pathname === '/login' || pathname === '/login/otp') {
    return null;
  }

  // Don't show header if not authenticated
  if (!isAuthenticated) {
    return null;
  }

  const handleLogout = () => {
    logout();
    router.push('/login');
  };

  return (
    <header className="bg-white/10 backdrop-blur-md border-b border-white/20 sticky top-0 z-50">
      <div className="max-w-md mx-auto px-4 py-3">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-2">
            <h1 className="text-xl font-bold text-white">QuizKaro</h1>
          </div>
          
          <div className="flex items-center space-x-4">
            <div className="bg-yellow-400 text-yellow-900 px-3 py-1 rounded-full font-semibold text-sm">
              ðŸª™ {user?.coins || 0}
            </div>
            <Button
              variant="ghost"
              size="sm"
              onClick={handleLogout}
              className="text-white hover:bg-white/20"
            >
              Logout
            </Button>
          </div>
        </div>
        
        {/* Navigation */}
        <nav className="mt-3">
          <div className="flex justify-between">
            <Button
              variant={pathname === '/home' ? 'secondary' : 'ghost'}
              size="sm"
              onClick={() => router.push('/home')}
              className="text-white hover:bg-white/20"
            >
              Home
            </Button>
            <Button
              variant={pathname === '/quiz' ? 'secondary' : 'ghost'}
              size="sm"
              onClick={() => router.push('/quiz')}
              className="text-white hover:bg-white/20"
            >
              Quiz
            </Button>
            <Button
              variant={pathname === '/wallet' ? 'secondary' : 'ghost'}
              size="sm"
              onClick={() => router.push('/wallet')}
              className="text-white hover:bg-white/20"
            >
              Wallet
            </Button>
            <Button
              variant={pathname === '/referral' ? 'secondary' : 'ghost'}
              size="sm"
              onClick={() => router.push('/referral')}
              className="text-white hover:bg-white/20"
            >
              Referral
            </Button>
            <Button
              variant={pathname === '/profile' ? 'secondary' : 'ghost'}
              size="sm"
              onClick={() => router.push('/profile')}
              className="text-white hover:bg-white/20"
            >
              Profile
            </Button>
          </div>
        </nav>
      </div>
    </header>
  );
};
