'use client';

import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';

interface User {
  id: string;
  name: string;
  mobile: string;
  coins: number;
  referralCode: string;
  hasCompletedDailyQuiz: boolean;
  hasUsedSpinToWin: boolean;
}

interface UserContextType {
  user: User | null;
  isAuthenticated: boolean;
  login: (mobile: string, name: string) => void;
  logout: () => void;
  updateCoins: (amount: number) => void;
  updateUser: (updates: Partial<User>) => void;
  markQuizCompleted: () => void;
  markSpinUsed: () => void;
  resetDailyLimits: () => void;
}

const UserContext = createContext<UserContextType | undefined>(undefined);

export const useUser = () => {
  const context = useContext(UserContext);
  if (context === undefined) {
    throw new Error('useUser must be used within a UserProvider');
  }
  return context;
};

interface UserProviderProps {
  children: ReactNode;
}

export const UserProvider: React.FC<UserProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  // Load user data from localStorage on mount
  useEffect(() => {
    const savedUser = localStorage.getItem('quizkaro_user');
    if (savedUser) {
      const userData = JSON.parse(savedUser);
      setUser(userData);
      setIsAuthenticated(true);
      
      // Check if it's a new day and reset daily limits
      const lastLoginDate = localStorage.getItem('quizkaro_last_login');
      const today = new Date().toDateString();
      
      if (lastLoginDate !== today) {
        setUser(prev => prev ? {
          ...prev,
          hasCompletedDailyQuiz: false,
          hasUsedSpinToWin: false
        } : null);
        localStorage.setItem('quizkaro_last_login', today);
      }
    }
  }, []);

  // Save user data to localStorage whenever user changes
  useEffect(() => {
    if (user) {
      localStorage.setItem('quizkaro_user', JSON.stringify(user));
    }
  }, [user]);

  const generateReferralCode = (mobile: string): string => {
    return `QK${mobile.slice(-4)}${Math.random().toString(36).substring(2, 6).toUpperCase()}`;
  };

  const login = (mobile: string, name: string) => {
    const newUser: User = {
      id: Date.now().toString(),
      name,
      mobile,
      coins: 50, // Welcome bonus
      referralCode: generateReferralCode(mobile),
      hasCompletedDailyQuiz: false,
      hasUsedSpinToWin: false,
    };
    
    setUser(newUser);
    setIsAuthenticated(true);
    localStorage.setItem('quizkaro_last_login', new Date().toDateString());
  };

  const logout = () => {
    setUser(null);
    setIsAuthenticated(false);
    localStorage.removeItem('quizkaro_user');
    localStorage.removeItem('quizkaro_last_login');
  };

  const updateCoins = (amount: number) => {
    setUser(prev => prev ? { ...prev, coins: prev.coins + amount } : null);
  };

  const updateUser = (updates: Partial<User>) => {
    setUser(prev => prev ? { ...prev, ...updates } : null);
  };

  const markQuizCompleted = () => {
    setUser(prev => prev ? { ...prev, hasCompletedDailyQuiz: true } : null);
  };

  const markSpinUsed = () => {
    setUser(prev => prev ? { ...prev, hasUsedSpinToWin: true } : null);
  };

  const resetDailyLimits = () => {
    setUser(prev => prev ? {
      ...prev,
      hasCompletedDailyQuiz: false,
      hasUsedSpinToWin: false
    } : null);
  };

  const value: UserContextType = {
    user,
    isAuthenticated,
    login,
    logout,
    updateCoins,
    updateUser,
    markQuizCompleted,
    markSpinUsed,
    resetDailyLimits,
  };

  return (
    <UserContext.Provider value={value}>
      {children}
    </UserContext.Provider>
  );
};
